body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: #ffe600 solid 1rem;
  border-top-width: 6rem;
  border-bottom: 0 none;
  min-height: 100vh;
  transition: 500ms;

}

.page {
  margin-top: -5rem
}

h2 {
font-family: "Pacifico", cursive;
    font-size: 3.5rem;
    text-align: center;
    color: #0080c7;
    color: rgb(189, 33, 48, 1);
    line-height: 4rem;
    text-shadow: 3px 3px 3px rgba(189, 33, 48, 0.35);
}


main {
  /* height: calc(100vh - 7rem);
  overflow-y: auto; */
}

header {
  height: 36vmin;
}

.logo,
.cloudline {
  background: url(./assets/logo-with-tagline.png) no-repeat center center;
  background: url(./assets/logo-with-tagline.svg) no-repeat center center;
  background-size: contain;
  height: 40vmin;
  width: 80vmin;
  margin: -3vmin auto 1rem;
  max-width: 900px;
}

.cloudline {
  background: url(./assets/SVG/cloudline.svg) no-repeat center top;
  position: absolute;
  z-index: 0;
  top: 3rem;
  height: 4rem;
  background-size: cover;
  left: 50%;
  transform: translateX(-50%);
}

.logo *,
.main-pic * {
  display: none;
}

nav {
  display: flex;
  width: calc(100vw - 2rem);
  justify-content: space-around;
  align-content: center;
  padding: 0;
  margin: auto;
  max-width: 1520px;
  height: 37vmin;
}

nav a {
  height: 35vmin;
  width: 44%;
  background-size: contain;
  background: no-repeat center;
  filter: opacity(.99) drop-shadow(0 0 1px #369) saturate(.75);
  clip-path: circle(50% at center);
  display: block;
  transform: rotate(var(--rotate, 0deg)) translateY(var(--translateY, 0))
    translateX(var(--translateX, 0)) scale(var(--scale, 1));
  /* position: absolute; */
}

.the-pies {
  background-image: url(./assets/SVG/starburst-1.svg);
  left: 0;
  --rotate: -15deg;
}

.track-us-down {
  background-image: url(./assets/SVG/starburst-2.svg);
  /* transform: rotate(16deg) translateY(-10%); */
  --rotate: 16deg;
  --translateY: -26%;
  --translateX: -7%;
}

.contact-us {
  right: -0;
  background-image: url(./assets/SVG/starburst-3.svg);
  --rotate: 7deg;
}

nav a:hover, nav a.active {
  filter: drop-shadow(0 0 4px rgb(91, 141, 190)) saturate(1);
  --scale: 1.1;
  transform: rotate(var(--rotate, 0deg)) translateY(var(--translateY, 0))
    translateX(var(--translateX, 0)) scale(var(--scale, 1));
}

nav a span {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  color: #fff;
  font-size: 6vmin;
  font-family: "Pacifico", cursive;
  text-align: center;
  width: 70%;
  transform: translateX(-50%) translateY(-50%);
  filter: drop-shadow(1px 2px 0 #369) drop-shadow(-1px -3px 0 #369) opacity(1) saturate(1);
  letter-spacing: 0.05rem;
  line-height: 1.1;
}

nav a span:active,
nav a.active span {
  color: #058;
  filter: drop-shadow(2px 2px 0 #fff) drop-shadow(-2px -2px 0 #fff) drop-shadow(1px 2px 0 #6cc) drop-shadow(-1px -3px 0 #369);
}

.the-pies span {
  transform: translateX(-45%) translateY(-90%) rotate(-16deg);
}

.track-us-down span {
  transform: translateX(-45%) translateY(-49%) rotate(-24deg);
}

.contact-us span {
  transform: translateX(-56%) translateY(-76%) rotate(14deg);
}

.main-pic {
  background: url(./assets/cone-pie-01-long-bg.jpg) no-repeat center 7%;
  background-size: cover;
  height: 40vmin;
  width: calc(100vw - 3rem);
  margin: 1rem auto 0;
  max-height: 500px;
  min-height: 250px;
}

.main-content {
  width: 80vw;
  max-width: 800px;
  text-align: center;
  margin: auto;
}

.main-content h1 {
  font-family: "Pacifico", cursive;
  font-size: 4.5rem;
  color: #0080c7;
  color: rgb(189, 33, 48, 1);
  line-height: 4rem;
  text-shadow: 3px 3px 3px rgba(189, 33, 48, 0.35);
}

.main-content p {
  font-family: "Tajawal", sans-serif;
  font-size: calc(15px + 1vmin);
  color: #000;
  font-weight: 500;
}

.birdie-type {
  font-family: "Pacifico", cursive;
  font-weight: 900;
  color: rgba(0, 128, 199, 1);
  font-size: 1.45rem;
  text-shadow: 3px 3px 0px rgba(0, 128, 199, 0.15);
  margin: 0.3rem;
}

.left-side-pic {
  float: left;
  width: 50vw;
  max-width: 540px;
  position: relative;
  left: 3rem;
  bottom: 1rem;
  margin: 1rem 4rem 1rem -36vw;
  transform: rotate(19deg);
  z-index: -9;
  animation: rotate-hand 13s 1s infinite;
}

@keyframes rotate-hand {
  0% { transform: rotate(19deg); }
  50% { transform: rotate(19deg); }
  52% { transform: rotate(29deg); }
  56% { transform: rotate(19deg); }
  100% { transform: rotate(19deg); }
}

.footer {
  background-color: #ffe600;
  height: 1rem;
  width: 100vw;
  position: fixed;
  bottom: 0;
}

@media only screen and (min-width: 470px) and (min-height: 470px) {
  .cloudline {
    height: 5rem;
  }

  nav {
    transform: scale(.95);
  }

  nav a {
    clip-path: circle(56% at center);
  }

  nav a span {
    filter: drop-shadow(2px 3px 0 #6cc) drop-shadow(-2px -4px 0 #369);
  }

  .left-side-pic {
    margin-left: -38vw;
    --hand-rotate: 12deg;
}
}

@media only screen and (min-width: 900px) and (min-height: 470px) {
  nav {
    transform: scale(.85);
  }
}

@media only screen and (min-width: 1200px) and (min-height: 470px) {
  nav {
    transform: scale(.8);
  }
  .left-side-pic {
    margin-left: -38vw;
    --hand-rotate: 29deg;
}

}